<template>
  <div class="register-container">
    <div class="title"></div>
    <el-form ref="form" :model="form" :rules="rules" status-icon label-width="80px">
      <el-form-item label="" prop="name">
        <el-input
          v-model.trim="form.name"
          minlength="2"
          maxlength="20"
          clearable
          placeholder=""
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model.trim="form.password" clearable type="password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="password1">
        <el-input v-model.trim="form.password1" clearable type="password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="btn-register" @click.native.prevent="onSubmit"></el-button>
      </el-form-item>
      <el-form-item>
        <el-button class="btn-login" @click.native.prevent="gotoLogin"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    // 
    let validateName = (rule, value, callback) => {
      clearTimeout(validateName.timer);
      if (value === "") {
        callback(new Error(""));
        return;
      }
      validateName.timer = setTimeout(async () => {
        if (!/^[0-9a-zA-Z_]{2,20}$/.test(value)) {
          callback(
            new Error("220")
          );
          return;
        }
        // 
        let res = await this.$axios({
          url: `/api/user?name=${value}`
        });
        if (res.data.ok) {
          callback(new Error(""));
          return;
        }
        callback();
      }, 500);
    };
    // 
    let validatePassword = (rule, value, callback) => {
      clearTimeout(validatePassword.timer);
      if (value === "") {
        callback(new Error(""));
        return;
      }
      validatePassword.timer = setTimeout(() => {
        if (!/^[0-9a-zA-Z_]{2,20}$/.test(value)) {
          callback(new Error("220"));
          return;
        }
        if (this.form.password1 !== "") {
          this.$refs.form.validateField("password1");
        }
        callback();
      }, 500);
    };
    // 
    let validatePassword1 = (rule, value, callback) => {
      clearTimeout(validatePassword1.timer);
      if (value === "") {
        callback(new Error(""));
        return;
      }
      validatePassword1.timer = setTimeout(() => {
        if (this.form.password !== value) {
          callback(new Error(""));
        }
        callback();
      }, 499);
    };
    return {
      form: {
        name: "",
        password: "",
        password1: ""
      },
      rules: {
        name: [{ validator: validateName, trigger: "change" }],
        password: [{ validator: validatePassword, trigger: "change" }],
        password1: [{ validator: validatePassword1, trigger: "change" }]
      }
    };
  },
  methods: {
    onSubmit() {
      this.$refs.form.validate(async valid => {
        if (valid) {
          let res = await this.$axios({
            method: "post",
            url: "/api/register",
            data: {
              user_name: this.form.name,
              user_password: this.form.password
            }
          });
          if (!res.data.ok) {
            this.$alert("", "", {
              confirmButtonText: ""
            });
          } else {
            this.$alert("", "", {
              confirmButtonText: ""
            }).then(
              () => {
// ====================================================================================================================
                // store  localStorage
                localStorage.setItem("user_name", this.form.name);
                this.$store.commit({
                  type: "saveUserName",
                  userName: this.form.name
                });
                this.$router.push({ name: "Home" });
              },
              () => {
                console.log("");
              }
            );
          }
        }
      });
    },
    gotoLogin() {
      this.$router.push({ name: "Login" });
    }
  }
};
</script>

<style>
.register-container {
  width: 1200px;
  margin: 0 auto;
  min-height: 567px;
  padding: 20px;
  margin: 0 auto;
  background: #fff;
  border: 1px solid #dcdfe6;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12),
    0 0 6px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
}
.register-container .title {
  text-align: center;
  font-size: 20px;
  padding-top: 20px;
}
.register-container .el-form {
  width: 500px;
  margin: 20px auto;
}
.register-container .btn-register,
.register-container .btn-login {
  width: 100%;
}
</style>