<template>
  <div class="user-container">
    <el-tabs v-model="activeName" tab-position="left" @tab-click="handleClick">
      <el-tab-pane label="" name="first">
        <div class="cart">
          <!--  -->
          <el-table :data="arrCartData" stripe border style="width: 100%">
            <el-table-column prop="name" label=""></el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">{{ scope.row.price | rmb }}</template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-input-number
                  v-model="scope.row.num"
                  @change="handleChange($event, scope.$index)"
                  :min="0"
                  :max="10"
                ></el-input-number>
              </template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">{{ scope.row.price * scope.row.num | rmb}}</template>
            </el-table-column>
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button
                  @click.native.prevent="deleteRow(scope.$index)"
                  type="text"
                  size="small"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="op-container">
            <div>{{ arrCartData.length }}</div>
            <div>{{ total | rmb }}</div>
            <div>
              
              <el-input
                class="order-remark"
                type="textarea"
                :rows="1"
                placeholder=""
                v-model="orderRemark"
              ></el-input>
            </div>
            <div>
              
              <span
                v-if="defaultAddress"
              >{{ defaultAddress.receiver_address }} | {{ defaultAddress.receiver_name }} | {{ defaultAddress.receiver_phone }}</span>
              <span v-else></span>
            </div>
            <el-button class="btn-buy" type="primary" @click.native.prevent="buyGoods"></el-button>
          </div>
        </div>
      </el-tab-pane>

      <el-tab-pane label="" name="second">
        <div class="default-address-title"></div>
        <div class="default-address">
          <div
            v-if="defaultAddress"
          >{{ defaultAddress.receiver_address }} | {{ defaultAddress.receiver_name }} | {{ defaultAddress.receiver_phone }}</div>
          <div v-else></div>
        </div>
        <div class="other-address-title"></div>
        <el-table :data="otherAddress" stripe border style="width: 100%">
          <el-table-column prop="receiver_address" label="" width="300px"></el-table-column>
          <el-table-column prop="receiver_name" label=""></el-table-column>
          <el-table-column prop="receiver_phone" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button @click.native.prevent="setDefaultAddress(scope.row.id)" type="text"></el-button>&nbsp;
              <el-button @click.native.prevent="deleteAddress(scope.row.id)" type="text"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="add-address">
          <el-button type="primary" @click.native.prevent="addAddress"></el-button>
        </div>
      </el-tab-pane>

      <el-tab-pane label="" name="third">
        <!--  -->
        <el-table :data="orderData" :row-class-name="tableRowClassName" border style="width: 100%">
          <el-table-column prop="order_num" label=""></el-table-column>
          <el-table-column prop="order_datetime" label=""></el-table-column>
          <el-table-column prop="order_remark" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">{{ scope.row.is_handle ? "" : "" }}</template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="checkOrdersDetail(scope.row.order_num)"
                type="text"
              ></el-button>&nbsp;
              <el-button
                v-if="scope.row.is_handle"
                @click.native.prevent="deleteOrder(scope.row.order_num)"
                type="text"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import { rmb } from "@/filters";
export default {
  filters: {
    rmb
  },
  data() {
    return {
      activeName: "first",
      // 
      orderRemark: "",
      // 
      defaultAddress: null,
      // 
      otherAddress: null,
      // 
      orderData: null
    };
  },
  computed: {
    // 
    total() {
      let res = 0;
      this.$store.state.arrCartData.forEach(item => {
        res += item.price * item.num;
      });
      return res;
    },
    // store
    arrCartData() {
      return this.$store.state.arrCartData;
    }
  },
  async created() {
    // queryactiveName
    if (this.$route.query.active) {
      this.activeName = this.$route.query.active;
    }
    // 
    let userName = this.$store.state.userName;
    // 
    let resReceiverInfo = await this.$axios({
      url: `/api/receiver?name=${userName}`
    });
    if (resReceiverInfo.data.ok) {
      let { defaultAddress, otherAddress } = resReceiverInfo.data.addressData;
      this.defaultAddress = defaultAddress;
      this.otherAddress = otherAddress;
    }
    // 
    this.getHistoryOrders();
  },
  methods: {
    // 
    handleClick(tab, event) {
      // console.log(tab, event);
    },
    // store
    deleteRow(index) {
      this.$store.commit({
        type: "deleteGoods",
        index
      });
    },
    // store
    handleChange(val, index) {
      if (val === 0) {
        this.deleteRow(index);
      }
    },
    // 
    async buyGoods() {
      // ====================================================================================================================
      let userName = this.$store.state.userName;
      // idnum
      let arrCartData = this.$store.state.arrCartData.map(item => {
        return {
          id: item.id,
          num: item.num
        };
      });
      // 
      if (arrCartData.length === 0) {
        this.$message({ message: "", type: "warning" });
        return;
      }
      if (!this.defaultAddress) {
        this.$confirm("", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        }).then(
          () => {
            this.$router.push({ name: "AddAddress" });
          },
          () => {}
        );
        return;
      }
      // 
      let res = await this.$axios({
        method: "post",
        url: "/api/order",
        data: {
          user_name: userName,
          order_remark: this.orderRemark,
          receiver_id: this.defaultAddress.id,
          arrCartData
        }
      });
      if (res.data.ok) {
        this.$message({ message: "", type: "success" });
        // 
        this.$store.commit("clearCart");
        // 
        await this.getHistoryOrders();
        // 
        this.activeName = "third";
        return;
      }
      this.$message({ message: "", type: "error" });
    },
    // 
    async setDefaultAddress(id) {
      let user_name = this.$store.state.userName;
      let res = await this.$axios({
        method: "put",
        url: "/api/receiver",
        data: {
          id,
          user_name
        }
      });
      if (res.data.ok) {
        this.$message({ message: "", type: "success" });
        //  
        let index = this.otherAddress.findIndex(item => item.id === id);
        let temp = this.defaultAddress;
        this.defaultAddress = this.otherAddress.splice(index, 1)[0];
        index = 0;
        while (
          index < this.otherAddress.length &&
          this.otherAddress[index++].id < temp.id
        );
        this.otherAddress.splice(index, 0, temp);
        return;
      }
      this.$message({ message: "", type: "error" });
    },
    // 
    deleteAddress(id) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(
        async () => {
          let res = await this.$axios({
            method: "delete",
            url: `/api/receiver?id=${id}`
          });
          if (res.data.ok) {
            this.$message({ message: "", type: "success" });
            let index = this.otherAddress.findIndex(item => item.id === id);
            this.otherAddress.splice(index, 1);
            return;
          }
          this.$message({ message: "", type: "error" });
        },
        () => {}
      );
    },
    // 
    addAddress() {
      this.$router.push({ name: "AddAddress" });
    },
    // className
    tableRowClassName({ row }) {
      if (!row.is_handle) {
        return "not-handle";
      }
      return "";
    },
    // 
    checkOrdersDetail(orderNum) {
      this.$router.push({ name: "OrderDetail", query: { id: orderNum } });
    },
    // 
    async deleteOrder(orderNum) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(
        async () => {
          let res = await this.$axios({
            method: "delete",
            url: `/api/order?num=${orderNum}`
          });
          if (res.data.ok) {
            this.$message({ message: "", type: "success" });
            // 
            let index = this.orderData.findIndex(
              item => item.order_num === orderNum
            );
            this.orderData.splice(index, 1);
            return;
          }
          this.$message({ message: "", type: "error" });
        },
        () => {}
      );
    },
    // 
    async getHistoryOrders() {
      let name = this.$store.state.userName;
      let res = await this.$axios({
        url: `/api/order?name=${name}`
      });
      if (res.data.ok) {
        this.orderData = res.data.orderData;
      }
    }
  }
};
</script>

<style>
.user-container {
  width: 1200px;
  padding: 20px;
  margin: 0 auto;
}
.user-container .el-tabs {
  min-height: 527px;
  padding: 20px;
  background: #fff;
  border: 1px solid #dcdfe6;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12),
    0 0 6px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
}
.user-container .op-container {
  padding: 20px;
  line-height: 60px;
}
.user-container .order-remark {
  width: 300px;
  vertical-align: middle;
}
.user-container .default-address-title,
.user-container .other-address-title {
  font-size: 20px;
  margin-bottom: 20px;
}
.user-container .default-address {
  padding-left: 20px;
  margin-bottom: 20px;
}
.user-container .add-address {
  margin-top: 20px;
}
.user-container .el-table .not-handle {
  background-color: oldlace;
}
</style>