import Vue from 'vue';
import VueRouter from 'vue-router';

import Home from '@/views/Home.vue'
import MainGoods from '@/views/MainGoods.vue';
import Login from '@/views/Login.vue';
import Register from '@/views/Register.vue';
import User from '@/views/User.vue';
import AddAddress from '@/views/AddAddress.vue';
import OrderDetail from '@/views/OrderDetail.vue';
import GoodsDetail from '@/views/GoodsDetail.vue';

import Admin from '@/views/admin/Admin.vue';
import AdminLogin from '@/views/admin/AdminLogin.vue';
import Goods from '@/views/admin/Goods.vue';
import EditGoods from '@/views/admin/EditGoods.vue';
import AddGoods from '@/views/admin/AddGoods.vue';
import Orders from '@/views/admin/Orders.vue';
import EditOrder from '@/views/admin/EditOrder.vue';

Vue.use(VueRouter)

const routes = [
  // 
  {
    path: '/',
    component: Home,
    children: [
      // 
      {
        path: '',
        name: 'Home',
        component: MainGoods
      },
      {
        path: 'login',
        name: 'Login',
        component: Login
      },
      {
        path: 'register',
        name: 'Register',
        component: Register
      },
      // 
      {
        path: 'user',
        name: 'User',
        // component: () => import(/* webpackChunkName: "user" */ '../views/User.vue')
        component: User,
        //  
        beforeEnter: (to, from, next) => {
          if (localStorage.getItem('user_name')) return next();
          next('/login');
        }
      },
      // 
      {
        path: 'address',
        name: 'AddAddress',
        component: AddAddress,
        beforeEnter: (to, from, next) => {
          if (localStorage.getItem('user_name')) return next();
          next('/login');
        }
      },
      // 
      {
        path: 'order',
        name: 'OrderDetail',
        component: OrderDetail,
        beforeEnter: (to, from, next) => {
          if (localStorage.getItem('user_name')) return next();
          next('/login');
        }
      },
      // 
      {
        path: 'detail',
        name: 'GoodsDetail',
        component: GoodsDetail,
        beforeEnter: (to, from, next) => {
          if (localStorage.getItem('user_name')) return next();
          next('/login');
        }
      }
    ]
  },
  // 
  {
    path: '/admin',
    component: Admin,
    //   
    beforeEnter: (to, from, next) => {
      if (localStorage.getItem('admin')) return next();
      next('/admin/login');
    },
    children: [
      //  
      {
        path: '',
        name: 'Admin',
        component: Goods
      },
      // 
      {
        path: 'editgoods',
        name: 'EditGoods',
        component: EditGoods
      },
      // 
      {
        path: 'addgoods',
        name: 'AddGoods',
        component: AddGoods
      },
      // 
      {
        path: 'orders',
        name: 'Orders',
        component: Orders
      },
      // 
      {
        path: 'eidtorder',
        name: 'EditOrder',
        component: EditOrder
      }
    ]
  },
  // 
  {
    path: '/admin/login',
    name: 'AdminLogin',
    component: AdminLogin
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router